* {
  box-sizing: border-box;
}

.page {
  margin-top: 6rem;
}

.h1 {
  color: $white;
  font-size: 2rem;
  text-align: center;
  text-shadow: $purple 4px 4px 0;
  text-transform: uppercase;
}

.h2 {
  color: $yellow;
  display: flex;
  &::before {
    content: url("data:image/svg+xml,%3Csvg width='18px' height='35px' viewBox='0 0 18 35' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cg id='lightning' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cpath d='M12.4642815,0.745102541 L0.112792483,22.2570232 C-0.251066646,22.8892234 0.322824988,23.6522931 1.01504479,23.4580023 L7.64733883,21.6067026 L4.43768717,33.6909881 C4.13101999,34.8496862 5.6791469,35.522154 6.28262058,34.4913052 L17.8846978,14.7018281 C18.2130585,14.1431161 17.7989099,13.4354143 17.1619099,13.4656149 L11.3164982,13.7374208 L15.1059579,1.96118192 C15.6502675,0.266925634 13.3477985,-0.793117066 12.4642815,0.745102541' fill='%23EFC319'%3E%3C/path%3E%3C/g%3E%3C/svg%3E");
    margin-right: 1rem;
  }
}

.h3 {
  color: $purple;
  font-size: 2rem;
  text-align: center;
  text-shadow: white 2px 2px 0;
  text-transform: uppercase;
}

.h4 {
  color: $white;
  font-size: 1.5rem;
  text-align: center;
  text-shadow: $purple 4px 4px 0;
  text-transform: uppercase;
}

.h5 {
  color: $white;
  font-size: 1rem;
  text-align: center;
  text-shadow: $purple 2px 2px 0;
  font-weight: bold;
}

.sub-text {
  font-size: 0.75rem;
}

.link {
  text-decoration: none;
}

.paragraph {
  color: $white;
  &.alt {
    color: $yellow;
    font-size: 0.75rem;
    font-weight: bold;
  }
}

.form-label {
  height: 1.5rem;
  @extend .paragraph.alt
}

.person {
  border: 4px solid $yellow;
  border-radius: 100%;
}

.input-box,
.input-button {
  border: 2px solid $yellow;
  color: $purple;
  font-size: 1rem;
  padding: 0.25rem 0.5rem;
  -webkit-appearance: none;
  -webkit-border-radius: 0px;
  height: 2.5rem;
  &.select {
    background-position: right;
    background-repeat: no-repeat;
    background-image: url("data:image/svg+xml,%3Csvg width='30px' height='30px' viewBox='0 0 30 30' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cg id='Symbols' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg id='Dropdown-with-label' transform='translate(-210.000000, -20.000000)'%3E%3Crect id='Rectangle' x='210' y='20' width='30' height='30'%3E%3C/rect%3E%3Cpolygon id='Line-11' stroke='%23EFC319' fill='%23EFC319' stroke-linecap='square' points='225 38 219 32 230.5 32'%3E%3C/polygon%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  }
}

.input-box:focus {
  outline: $white 5px auto;
}

.input-button {
  background: $yellow;
}

.container {
  width: 90%;
  margin-left: auto;
  margin-right: auto;

  @media only screen and (min-width: $breakpoint-small) {
    width: 80%;
  }

  @media only screen and (min-width: $breakpoint-large) {
    width: 75%;
    max-width: 60rem;
  }
}

.sticky {
  position: sticky;
  top: 2rem;
}

.content-container {
  background: $purple-transparent;
  margin: -6rem 0 -3rem;
  padding: 7rem 2rem 4rem;
  display: flex;
  flex-direction: column;
}

.flex {
  display: flex;
}

.flex-col {
  @extend .flex;
  flex-direction: column;
}

.flex-row {
  @extend .flex;
  flex-direction: row;
}

.flex-col-center {
  @extend .flex-col;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.flex-row-wrap {
  @extend .flex-row;
  flex-wrap: wrap;
}

.flex-row-center {
  @extend .flex-row;
  justify-content: center;
}

.job-fact-container {
  margin-top: 4rem;
  margin-bottom: 4rem;
  font-size: 1.25rem;
}

.news-logo {
  margin: 0 10%;
  width: 80%;
}

.img-full {
  width: 100%;
}
