.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  padding: 0;
  box-shadow: 0 0 5px $purple;
  z-index: 5500;
  .link {
    font-family: 'Cantarell', sans-serif;
    text-shadow: $purple 2px 2px 0;
  }
}

.nav {
  overflow: hidden;
  max-height: 2.5rem;
  transition: max-height 0.5s ease-out;
  &:hover {
    max-height: 1000px;
  }
}

.header,
.footer {
  .container {
    padding: 0 1rem;
  }
}

.nav-bar {
  background: $purple;
  height: 2.5rem;
  display: flex;
  align-items: center;
  .nav-logo {
    height: 1.5rem;
  }
}

.nav-bar-dropdown {
  background: $purple-transparent;
  padding-bottom: 2rem;
}

.footer {
  background: $purple;
  padding: 2rem 0;
  position: relative;
  z-index: 5000;
  .input-box {
    font-size: 0.75rem;
    flex-grow: 1;
  }
  .input-button {
    font-size: 0.75rem;
  }
}

.footer-paragraph {
  font-size: 0.75rem;
  margin-top: 0;
}

.footer-subscribe {
  margin-block-start: 1em;
  margin-block-end: 1em;
}

.footer-logo-container {
  display: flex;
  flex-wrap: wrap;
}

.footer-logo {
  height: 2.5rem;
  margin: 2rem 2rem 2rem 0;
}

.nav-link-list {
  padding: 0;
}

.nav-link {
  font-size: 0.75rem;
  list-style: none;
  margin-bottom: .5rem;
  white-space: nowrap;
}