html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

[bp~='container'] {
  width: 100%;
  margin: 0 auto;
  display: block;
  max-width: 1000px;
}

[bp~='grid'] {
  display: grid !important;
  grid-gap: 16px;
  grid-template-columns: repeat(12, 1fr);
}

[bp~='vertical-start'] {
  align-items: start;
}

[bp~='vertical-center'] {
  align-items: center;
}

[bp~='vertical-end'] {
  align-items: end;
}

[bp~='between'] {
  justify-content: center;
}

[bp~='gap-none'] {
  grid-gap: 0;
  margin-bottom: 0;
}

[bp~='gap-column-none'] {
